import React, {Component} from 'react' ;
import './Menu.css'
import icon from'../../../assets/menu.svg'
import xicon from '../../../assets/menu-x.svg'

class Menu extends Component {
    state={
        show: false,
        srcicon: icon
    };

    
    menu = () => {
        this.setState(current=>({show:!current.show}));
        var ligther = true
        if (this.state.srcicon==icon){
            ligther = false
        }
        switch (ligther) {
            case false:
                this.setState({srcicon:xicon});
                break;
            case true:
                this.setState({srcicon:icon});
                break;
        
            default:
                break;
        }
        
    }

    render(){
        return(
            <div>
                <button  onClick={this.menu} className="button-hamburgerIcon">
                    <img className="img-hamburger" src={this.state.srcicon} /></button>


                    {this.state.show ? ( 
                                <div className="div-menu">  
                                    <ul>
                                        <li>
                                            <a className="suport" href="">Support</a>
                                        </li>
                                        <li>
                                            <a className="more"href="">More content</a>
                                        </li>
                                        <li>
                                            <a className="learn" href="">Learn Code</a>
                                        </li>
                                        <li>
                                            <a className="resources" href="">Resources</a>
                                        </li>
                                        <li>
                                            <a className="about" href="">About</a>
                                        </li>
                                        <li>
                                            <a className="donate" href="">Donate</a>
                                        </li>
                                        
                                    </ul>
                                    <div className="separador"></div>
                                </div>):( null )}

            </div>
        );
    }
}
export default Menu